/* 中画面 */
@media only screen and (min-width:651px) {
  #whitebase {
    display: -webkit-flex;
    display: flex; /* flex設定 */
  }

  .side-part {
    margin-right: 1rem;
    max-width: 300px;
  }

  .main-contents {
  }
}

